import { FunctionComponent, ReactElement, ReactFragment } from "react";

export const Instruction: FunctionComponent = (): ReactElement<ReactFragment> => {
    return(
        <ul>
            <li>
                Для того, чтобы закодировать текст, сперва его нужно ввести в левое поле во вкладке "Защифровать".
                &nbsp;<strong>Правое поле недоступно для ввода и редактирования!</strong>
            </li>
            <li>
                После редактирования левого поля нужно нажать ниже кнопку "Перевести". Если все успешно, то рядом
                появится еще одна маленькая кнопка с ключиком, а в поле справа появится закодированный текст.
            </li>
            <li>
                Зашифрованный текст можно скопировать, если нажать кнопку в нижнем правом углу внутри самого поля.
            </li>
            <li>
                Также можно скопировать и ключ, необходимый для расшифровки Вашего сообщения. Для этого нужно
                нажать на ключик рядом с кнопкой "Перевести".
                <br />
                <strong>ВНИМАНИЕ!</strong> Ключ дешифровки одноразовый. При повторном нажатии кнопки "Перевести"
                будет сгенерирован новый ключ и исходный текст будет зашифрован заново.
            </li>
            <li>
                Если в левом поле нажать кнопку с крестиком, то все поля, включая и ключ дешифровки, будут очищены.
            </li>

            <br /><br />

            <li>
                Для расшифровки текста во вкладке "Расшифровать" в <strong>большое поле слева</strong> 
                &nbsp;нужно вставить зашифрованный текст.
            </li>
            <li>
                А <strong>в маленькое поле слева</strong> нужно вставить ключ дешифровки.
            </li>
            <li>
               <strong>Важно не перепутать</strong> поля, иначе ничего расшифровать не удастся!
            </li>
            <li>
                После того, как поля заполнены, нужно нажать кнопку "Перевести" и, если все поля заполнены верно,
                справа появится расшифрованный текст.
            </li>
            <li>
                Нажатие любого из крестиков приведет к очистке соответсвующего поля, а также поля с расшифрованным
                текстом, если он там есть.
            </li>

        </ul>

    )

}    
