import { FunctionComponent, useRef, useState } from 'react';
import iInputMultiline from '../interfaces/common/iInputMultiline';
import { TextAreaElement } from '../types';

const InputMultiline: FunctionComponent<iInputMultiline> = 
    ({ id, placeholder, className, disabled, value, onInput }): TextAreaElement => {
    
    const input = useRef<HTMLTextAreaElement>(null)

    const [ isFocused, setFocused ] = useState<boolean>(false)

    return(
        <textarea 
            id={id} 
            ref={input}
            className={className} 
            placeholder={isFocused || value ? '' : placeholder} 
            disabled={disabled} 
            onInput={() => onInput!(input.current!.value)} 
            value={value} 
            onFocus={() => setFocused(true)}
            onBlur={() => setFocused(false)}
        />
    )
}

export default InputMultiline