import { FunctionComponent } from "react";
import iTabBtn from "../interfaces/common/iTabBtn";
import '../App.scss'
import { ParagraphElement } from "../types";

export const TabBtn: FunctionComponent<iTabBtn> = 
    ({ children, titleText, onSetActiveTab, id, active }): ParagraphElement => {
    
        return (
        <div 
            className="bar_btn" 
            style={active ? activeClass : {}} 
            onClick={() => onSetActiveTab(id)}
        >    
            {titleText}
            {children}
        </div>
    )
} 

const activeClass = {
    backgroundColor: 'rgba(43, 0, 61, .6)',
    border: '2px solid #9400d3',
    transition: 'all .35s'
}