import { FunctionComponent, useState } from 'react'
import { Bar } from './components/Bar'
import iTabs from './interfaces/common/iTabs'
import Encryption from './components/Encryption'
import Decryption from './components/Decryption'
import { Instruction } from './components/Instruction'
import './App.scss'
import { DivElement } from './types'

const App: FunctionComponent = (): DivElement => {

  	const [ tabs, activateTab ] = useState<iTabs[]>([
		{ title: 'Зашифровать',  active: true, id: 'enc', iconCSSClass: 'icon-lock' },
		{ title: 'Расшифровать',  active: false, id: 'dec', iconCSSClass: 'icon-unlock'  },
		{ title: 'Инструкция',  active: false, id: 'instr', iconCSSClass: 'icon-instruction' },
  	])

	const setActiveTab = (id: string): void => {
		activateTab(
			tabs.map(tab => {
				tab.active = false
				if(tab.id === id) tab.active = !tab.active

				return tab
			})
		)
	}

	return (
		<div 
			className="App"
		>
			<Bar 
				tabs={tabs} onSetActiveTab={setActiveTab} 
			/>

			<div 
				id="encryption" 
				style={tabs[0].active ? display.block : display.none}
			>
				<Encryption />
			</div>

			<div 
				id="decryption" 
				style={tabs[1].active ? display.block: display.none}
			>
				<Decryption />
			</div>

			<div 
				id="instruction" 
				style={tabs[2].active ? display.block : display.none}
			>
				<Instruction />
			</div>
		</div>	
	)
}

const display = {
	block: {
		display: 'block',
	},

	none: {
		display: 'none'
	},
}

export default App;
