import { FunctionComponent } from 'react'
import iBtnContainer from '../interfaces/common/iBtnContainer'
import '../App.scss'
import { Fragment } from '../types'

const BtnContainer: FunctionComponent<iBtnContainer> = ({ buttons, onTranslate }): Fragment => {
    return (
        <div className='btn-container'>
            {
                buttons.map(btn => {
                    return (
                        <button className={btn.className}
                            id={btn.id}
                            key={Math.random() * 1e20}
                            onClick={(e) => {
                                    if((e.target as HTMLButtonElement).id === buttons[0].id) onTranslate() 
                                } 
                            }
                        >
                            {btn.title}
                        </button>
                    )
                })
            }
        </div>
    )
}

export default BtnContainer