import { FunctionComponent } from "react";
import iIcon from "../interfaces/common/iIcon";
import { DivElement } from "../types";

const Icon: FunctionComponent<iIcon> = 
    ({ svg, imageSource, imageAltAttributeValue, iconFontClass, fontSize }): DivElement => {
    
        return (
        <div
            style={{
                width: '100%',
                height: '100%',
                display: 'inline-flex', 
                justifyContent: 'center', 
                alignItems: 'center'
            }}
        >
            {svg}

            <img 
                src={imageSource} 
                alt={imageAltAttributeValue} 
            />

            <i
                className={iconFontClass}
                style={{
                    fontSize: fontSize + 'px',
                    fontWeight: 'normal',
                }}
            />
        </div>
    )
}

export default Icon