import { FunctionComponent, useState } from "react";
import { CopyToClipboard } from 'react-copy-to-clipboard';
import iEncryptionBtns from "../interfaces/Encryption/iEncryptionBtns";
import iTextFields from "../interfaces/Encryption/iTextFields";
import iInvisibleBtn from "../interfaces/Encryption/iInvisibleBtn";
import DATABASE from "../kernel/DATABASE";
import getHashedDataBase from "../kernel/getHashedDataBase";
import getPositionsOfEntries from "../kernel/getPositionsOfEntries";
import InputMultiline from "./InputMultiline";
import StandbyBtn from "./StandbyBtn";
import BtnContainer from "./BtnContainer";
import { Fragment } from "../types";
import Icon from "./Icon";

const Encryption: FunctionComponent = (): Fragment => {

    //BUTTONS 

    const buttons: iEncryptionBtns[] = [{ title: 'Перевести', className: 'btn', id: 'traslate-encr' }]

    //DECRYPTION KEY BUTTON

    const [ visible, setVisible ] = useState<iInvisibleBtn>({ visible: false })
    
    //ENTER TEXT

    const [ entryText, setEntryText ] = useState<string>('')

    const getEntry = (text: string): void => {
        setEntryText(text)
    }

    //OUTER TEXT

    const [ outerText, setOuterText ] = useState<iTextFields>({ content: '', copied: false })

    //DECRYPTION KEY 

    const [ encrKey, setEncrKey ] = useState<iTextFields>({ content: '', copied: false })

    
    
    const Encrypt = (): void => {

        const [ newDataBase, randomNumCollection24 ] = getHashedDataBase(DATABASE())
    
        const incomings: String[] = getPositionsOfEntries(newDataBase, entryText)
    
        const encrypted: string[] = incomings.map(elem => {
            return `${Number(elem).toString(24)}`.toUpperCase()
        })
    
        setOuterText({ ...outerText, content: encrypted.join('o').toUpperCase() })
        setEncrKey({ ...encrKey, content: randomNumCollection24.join('o').toUpperCase() })

    }

    const onTranslate = (): void | boolean => {

        if(entryText) {
            setVisible({ visible: true })
            Encrypt()
        } else {
            return false
        }

    }

    //CLEAR ALL FIELDS AND HIDE KEY BUTTON
    
    const clearStates = (): void => {

        setEntryText('')
        setOuterText({ content: '', copied: false })
        setVisible({ visible: false })
        setEncrKey({ content: '', copied: false })

    }
    
    return (
        <>
            <div 
                className="container"
            >
                <div 
                    className="field-wrapper"
                >
                    <InputMultiline 
                        id="inputEncr" 
                        className="field" 
                        placeholder="Введите текст сюда" 
                        onInput={getEntry} 
                        value={entryText}
                        disabled={false} 
                    />
                    <StandbyBtn 
                        id="clear" 
                        className="btn btn-small btn-standby" 
                        title="Очистить поле ввода" 
                        onClick={clearStates}
                        visible={true}
                    >
                        <Icon
                            iconFontClass='icon-clear'
                            fontSize={20}
                        />
                    </StandbyBtn>
                </div>
                
                <div 
                    className="field-wrapper"
                >
                    <InputMultiline 
                        id="outputEncr" 
                        className="field field-disabled" 
                        disabled={true}
                        placeholder="Это поле нельзя редактировать. Здесь появится зашифрованный текст" 
                        value={entryText ? outerText.content : ''} 
                    />
                    <CopyToClipboard 
                        text={outerText.content} 
                        onCopy={() => setOuterText({ ...outerText, copied: true })} 
                    >
                        <StandbyBtn 
                            className="btn btn-small btn-standby" 
                            id="clipboard" 
                            title="Нажмите, чтобы скопировать в буфер обмена"
                            visible={true} 
                        >
                            <Icon
                                iconFontClass={outerText.copied ? 'icon-check' : 'icon-clipboard'}
                                fontSize={outerText.copied ? '25' : '16'}
                            />
                        </StandbyBtn>
                    </CopyToClipboard>
                </div>
            </div>
            
            <div 
                className="btn-wrapper"
            >
                <BtnContainer 
                    buttons={buttons} 
                    onTranslate={onTranslate}
                />

                <CopyToClipboard 			
                    text={encrKey.content}
                    onCopy={() => setEncrKey({ ...encrKey, copied: true })} 
                >
                    <StandbyBtn
                        className="btn btn-small"
                        id="encryptionKey"
                        visible={visible.visible}
                    >
                        <Icon 
                            iconFontClass={encrKey.copied ? 'icon-check' : 'icon-key'}
                            fontSize={25}
                        />
                    </StandbyBtn>
                </CopyToClipboard>
            </div>
        </>
    )
}

export default Encryption