import { FunctionComponent, useRef, useState } from "react"
import { CopyToClipboard } from "react-copy-to-clipboard"
import InputMultiline from "./InputMultiline"
import StandbyBtn from "./StandbyBtn"
import DATABASE from "../kernel/DATABASE"
import iEntryKey from "../interfaces/Decryption/iEntryKey"
import iEncrText from "../interfaces/Decryption/iEncrText"
import iTextFields from "../interfaces/Encryption/iTextFields"
import { Fragment } from "../types"
import Icon from "./Icon";

const Decryption: FunctionComponent = (): Fragment => {

    //KEY INPUT PLACEHOLDER

    const [ isFocused, setFocused ] = useState<boolean>(false)
    
    //DECRYPTION KEY

    const keyInput = useRef<HTMLInputElement>(null)

    const [ entryKey, setEntryKey ] = useState<iEntryKey>({ key: '' })
    
    const deleteKey = (): void => {
        setEntryKey({ key: '' })
        setDecrText({ content: '', copied: false })
    }

    //ENCRYPTED TEXT

    const [ encrText, setEncrText ] = useState<iEncrText>({ content: '' })
    
    const getText = (text: string): void => {
        setEncrText({ content: text })
    }

    const deleteEncrText = (): void => {
        setEncrText({ content: '' })
        setDecrText({ content: '', copied: false })
    }

    //ADD IN THE STATE A KEY FROM A USER

    const getKey = (): void => {
        setEntryKey({ key: keyInput.current!.value })
    }

    const [ decrText, setDecrText ] = useState<iTextFields>({ content: '', copied: false })


    const getHashTable = (): string[] => {

        const dataBase: String[] = DATABASE()

        const hashTable: string[] = dataBase.map(elem => {
            return dataBase.indexOf(elem).toString(24).toUpperCase()
        })

        return hashTable
    }

    const getHashKey = (): number[] => {

        const hash: string[] = getHashTable()
        const key: string[] = entryKey.key.split('O')

        const decryptionKey : number[] = key.map(elem => {
            return hash.indexOf(elem)
        })

        return decryptionKey
    }

    const decrypt = (): void => {

        const table: string[] = getHashTable()
        const key: number[] = getHashKey() //decimal
        const dataBase: String[] = DATABASE()
        const txt: string[] = encrText.content.split('O') //encrypted

        //десятичные, массив совпадений в хеше

        const incomings: number[] = txt.map(elem => {
            return table.indexOf(elem)
        }) 

        const outputTxt: String[]  = incomings.map(elem => {
            return dataBase[ key[ elem ] ]
        })

        setDecrText({ content: outputTxt.join(''), copied: false })
    }

    return(
        <>
            <div 
                className="container container-decr"
            >
                <div 
                    className="decryptionLeftFieldsWrapper"
                >
                    <div 
                        className="field-wrapper"
                    >
                        <InputMultiline 
                            id="inputDecr"
                            className="field" 
                            value={encrText.content}
                            placeholder="Введите зашифрованный текст сюда"
                            onInput={getText} 
                            disabled={false}
                        />
                        <StandbyBtn
                            id="resetVal"
                            className="btn btn-small btn-standby"
                            title="Очистить поле"
                            onClick={deleteEncrText}
                            visible={true} 
                        >
                            <Icon
                                iconFontClass='icon-clear'
                                fontSize={20}
                            />
                        </StandbyBtn>
                    </div>
                    
                    <div 
                        className="field-wrapper"
                    >
                        <input
                            type="text" 
                            id="key" 
                            ref={keyInput} 
                            className="field" 
                            placeholder={isFocused || entryKey.key ? '' : "Введите ключ дешифровки"}
                            value={entryKey.key}
                            onInput={getKey}
                            onFocus={() => setFocused(true)}
                            onBlur={() => setFocused(false)}
                        />
                        <StandbyBtn 
                            id="pasteFromClipboard"
                            className="btn btn-small btn-standby"
                            title="Очистить поле"
                            onClick={deleteKey}
                            visible={true}
                        >
                            <Icon
                                iconFontClass='icon-clear'
                                fontSize={20}
                            />
                        </StandbyBtn>
                    </div>
                </div>

                <InputMultiline 
                    id="outputDecr" 
                    className="field field-disabled" 
                    placeholder="Здесь появится расшифрованный текст" 
                    value={encrText.content && entryKey.key ? decrText.content : ''}
                    disabled={true}
                />
                <CopyToClipboard
                    text={decrText.content}
                    onCopy={()=> setDecrText({ ...decrText, copied: true })}
                >
                    <StandbyBtn
                        id="copyVal" 
                        className="btn btn-small btn-standby"
                        title="Нажмите, чтобы скопировать в буфер обмена"
                        visible={true}
                    >
                        <Icon
                            iconFontClass={decrText.copied ? 'icon-check' : 'icon-clipboard'}
                            fontSize={decrText.copied ? '25' : '16'}
                        />
                    </StandbyBtn>
                </CopyToClipboard>
            </div>

            <div 
                className="btn-wrapper"
            >
                <div className="btn-container">
                    <button 
                        className="btn" 
                        id="translate-decr"
                        onClick={decrypt}
                    >
                        Перевести
                    </button>
                </div>
            </div>
        </>
    )
    
}

export default Decryption