const getHashedDataBase = (data: String[]): String[][] => {

    const randomNumCollection = new Set<number>()
    const randomNumCollection24 = new Array<String>()
    const newDataBase = new Array<String>()

    const getRandomNum = (min: number, max: number): number => {   
        return Math.floor(Math.random() * (max - min + 1 )) + min
    }

    while (randomNumCollection.size !== data.length) {
        randomNumCollection.add(getRandomNum(0, data.length - 1)) 
    }
    
    for (let index of randomNumCollection) {
        newDataBase.push(data[index]);
        randomNumCollection24.push(index.toString(24))
    }

    return [ newDataBase, randomNumCollection24 ]
}

export default getHashedDataBase