import { FunctionComponent } from 'react';
import '../App.scss';
import iStandbyBtn from '../interfaces/common/iStandbyBtn';
import { ButtonElement } from '../types';

const StandbyBtn: FunctionComponent<iStandbyBtn> =
    ({ children, className, id, title, onClick, visible }): ButtonElement => {

    return (
        <button className={className}
            id={id}
            title={title}
            onClick={onClick}
            style={visible ? display.default : display.none}
        >
            {children}
        </button>
    )
}

const display = {
    default : {
        display: ' '
    },

    none: {
        display: 'none'
    }
}

export default StandbyBtn
