const DATABASE = (): String[] => {

    const RU_LowerCase: string = 'абвгдеёжзийклмнопрстуфхцчшщъыьэюя'
    const RU_UpperCase: string = 'АБВГДЕЁЖЗИЙКЛМНОПРСТУФХЦЧШЩЪЫЬЭЮЯ'
    const EN_LowerCase: string = 'abcdefghijklmnopqrstuvwxyz'
    const EN_Uppercase: string = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'
    const Numbers: string = '0123456789'
    const Signs: string = ' !?.-+=,(—)\'%\n@#№;*^:/"' //the frist symbol is SPACE
    
    return [
        ...RU_LowerCase, 
        ...RU_UpperCase, 
        ...Numbers, 
        ...Signs,
        ...EN_LowerCase, 
        ...EN_Uppercase
    ]
}

export default DATABASE;