import { FunctionComponent } from "react";
import { TabBtn } from './TabBtn'
import iBar from "../interfaces/common/iBar";
import { DivElement } from "../types";
import Icon from "./Icon";

const windowWidth = document.documentElement.clientWidth;

export const Bar: FunctionComponent<iBar> = ({ tabs, onSetActiveTab}): DivElement => {
    
    return (  
        <div className="bar">
            {
                tabs.map((tab, index) => {
                    return (
                        <TabBtn 
                            id={tab.id}
                            titleText={windowWidth >= 768 ? tab.title : ''}
                            key={index}
                            active={tab.active}
                            onSetActiveTab={onSetActiveTab}                         
                        >
                            <Icon
                                iconFontClass={windowWidth < 768 ? tab.iconCSSClass : ''}
                                fontSize={25}
                            />
                        </TabBtn>
                    )
                })
            } 
        </div>
    )
}